var serviceHead = document.querySelector(".main__service-list-characteristic_popup");
var serviceHeadConsolidate = document.querySelector(".main__service-list-characteristic_spacer");

if(serviceHeadConsolidate){
	window.addEventListener("scroll", function move(){	
		if(serviceHead.getBoundingClientRect().top <= 6){
			serviceHead.classList.remove("main__service-list-characteristic_show");
			serviceHeadConsolidate.classList.add("main__service-list-characteristic_show");
		};
		if(serviceHead.getBoundingClientRect().top >= 6){
			serviceHead.classList.add("main__service-list-characteristic_show");
			serviceHeadConsolidate.classList.remove("main__service-list-characteristic_show");
		};
	});
};

var toTop = document.querySelector(".to-top");

if(toTop){
	toTop.addEventListener("click", function(){
		window.scrollTo(0, 0);
	});
};	


var tableScrollNext = document.getElementById("scrollnext");
var tableScrollPre = document.getElementById("scrollpre");
var numScroll = document.querySelectorAll(".server-info-scroll-num");
var tableServiceScroll = document.querySelectorAll(".list-service-scroll");
var x = 0;

tableScrollNext.addEventListener("click", function(e){
	numScroll[0].classList.remove("main__server-info-scroll-num_show");
	tableScrollPre.classList.add("main__server-info-scroll_active");
	for(var i = 0; i < numScroll.length; i++){
		if(numScroll[2].value < 4) {
			numScroll[i].value = +numScroll[i].value + 1; 
			for(var z = 0; z < tableServiceScroll.length; z++){
				tableServiceScroll[z].style.right = x + 624 + "px";
			};
		};
		if(numScroll[2].value == 4){
			numScroll[2].classList.add("main__server-info-scroll-num_show");
			tableScrollNext.classList.remove("main__server-info-scroll_active");
		};
	};
	if(x < 1248) {
		x = x + 624;
		console.log(x);
	}	
});
tableScrollPre.addEventListener("click", function(e){
	numScroll[2].classList.remove("main__server-info-scroll-num_show");
	tableScrollNext.classList.add("main__server-info-scroll_active");
	for(var i = 0; i < numScroll.length; i++){
		if(numScroll[2].value > 2) {
			numScroll[i].value = +numScroll[i].value - 1;
			for(var z = 0; z < tableServiceScroll.length; z++){
				tableServiceScroll[z].style.right = x - 624 + "px";
			};
		};
		if(numScroll[2].value == 2){
			numScroll[0].classList.add("main__server-info-scroll-num_show");
			tableScrollPre.classList.remove("main__server-info-scroll_active");

		};
	};
	if(x > 0) {
		x = x - 624;
		console.log(x);
	}
});

var serviceHeadHidden = document.querySelector(".main__service-list-characteristic_hidden");

if(!serviceHeadConsolidate) {
	window.addEventListener("scroll", function move(){
			console.log(serviceHeadHidden.getBoundingClientRect().top);

		if(serviceHeadHidden.getBoundingClientRect().top < 20){
			console.log("scroll");
			serviceHead.classList.add("main__service-list-characteristic_show-scroll");
			serviceHead.classList.remove("main__service-list-characteristic_pos");
		};
		if(serviceHeadHidden.getBoundingClientRect().top > 20){
			console.log("fixed");
			serviceHead.classList.remove("main__service-list-characteristic_show-scroll");
			serviceHead.classList.add("main__service-list-characteristic_pos");
		};
	});
}